import wPicker from "@/components/w-picker/w-picker.vue";
import uniIcons from "@/components/uni-icons/uni-icons.vue";
import empty from "@/components/empty";

export default {
	components: {
		wPicker,
		uniIcons,
		empty,
		// zySearch
	},
	data() {
		return {
			searchText:'',		
			isFocus: false,
			
			selectList: [
				{default: '类型', ref: 'typeselector'},
				{default: '难度', ref: 'levelselector'},
				{default: '排序', ref: 'sortselector'}
			],
			
			type: '类型',
			typeList:[
				{
					label: "全部",
					value: 1
				}
				,{
					label: "单音",
					value: 2
				},
				{
					label: "指弹",
					value: 3
				},
				{
					label: "弹唱",
					value: 4
				},
				{
					label: "合奏",
					value: 5
				}
			],
			
			level: '难度',
			levelList:[
				{
					label: "全部",
					value: 1
				}
				,{
					label: "萌新",
					value: 2
				},
				{
					label: "入门",
					value: 3
				},
				{
					label: "进阶",
					value: 4
				},
				{
					label: "困难",
					value: 5
				},
				{
					label: "大神",
					value: 6
				}
			],
			
			sort: '排序',
			sortList:[
				{
					label: "综合",
					value: 'mix'
				}
				,{
					label: "时间",
					value: 'created_at'
				},
				{
					label: "热度",
					value: 'views'
				},
				{
					label: "曲名",
					value: 'name'
				}
			],
			
			musicList: [],
			loaded: false,
			isLoading: false,
		}
	},
	onLoad: function() {
		this.getData()
	},
	methods: {
		showSelector: function(index){
			let item = this.selectList[index];
			this.$refs[item.ref].show();
		},
		onTypeConfirm(val){
			console.log(val);
			this.type = val.result;
			this.getData()
		},
		onLevelConfirm(val){
			console.log(val);
			this.level = val.result;
			this.getData()
		},
		onSortConfirm(val){
			console.log(val);
			this.sort = val.result;
			this.getData()
		},
		getData: function() {
			console.log(this.searchText)
			console.log(this.type)
			console.log(this.level)
			console.log(this.sort)
			let type = (this.type == '全部' || this.type == '类型')? '' : this.type
			let level = (this.level == '全部' || this.level == '难度')? '' : this.level
			let sort = (this.sort == '排序')? 'mix' : this.sort
			
			// 根据页面选择的条件 调用api从服务器端获取数据
			this.$http.request({
				url: '/music/getMusicList',
				method: 'post',
				header: {},
				params: {
					page: 1,
					pagesize: 10,
					name: this.searchText,
					type: type,
					level: level,
					order: sort
				}
			}).then(res => {
				console.log(res)
				console.log(res.data.data)
				this.musicList = res.data.data.data
			}).catch(err => {
				console.log(err)
			})
		},
		
		searchStart: function() {	//触发搜索
			let _this = this;
			if (_this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else{
				console.log(_this.searchText)
				_this.getData()
				// uni.getStorage({
				// 	key:'search_cache',
				// 	success(res){
				// 		let list = res.data;
				// 		console.log(list);
				// 		if(list.length > 5){
				// 			for(let item of list){
				// 				if(item == _this.searchText){
				// 					return false;
				// 				}
				// 			}
				// 			list.pop();
				// 			list.unshift(_this.searchText);
				// 		}else{
				// 			for(let item of list){
				// 				if(item == _this.searchText){
				// 					return false;
				// 				}
				// 			}
				// 			list.unshift(_this.searchText);
				// 		}
				// 		uni.setStorage({
				// 			key: 'search_cache'
				// 		});
				// 	},
				// 	fail() {
				// 		uni.setStorage({
				// 			key: 'search_cache'
				// 		});
				// 	}
				// })
			}
		},
		startRecognize: function() {	//语音输入
			let _this = this;
			let options = {};
			options.engine = 'iFly';
			options.punctuation = false; // 是否需要标点符号 
			options.timeout = 10 * 1000;
			plus.speech.startRecognize(options, function(s) {
				_this.searchText = _this.searchText + s;
			});
		},
		
		gotoinfo: function(e) {
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid=' + newsid,
				success: res => {
					console.log('gotoinfo success')
				},
				fail: () => {
					console.log('gotoinfo fail')
				},
				complete: () => {
					console.log('gotoinfo complete')
				}
			});
		},
	}
}
